const { floor, round } = Math;

/**
 * Returns `min - N*wrapSpan`, where `N` is a whole number and the returned
 * value is in `[ min, min + wrapSpan )`.
 */
export function wrapAbove( value: number, min: number, wrapSpan: number ): number {
    const wrapCount = floor( ( value - min ) / wrapSpan );
    return ( value - ( wrapCount * wrapSpan ) );
}

/**
 * Returns `ref - N*wrapSpan`, where `N` is a whole number and the returned
 * value is in `[ ref - 0.5*wrapSpan, ref + 0.5*wrapSpan )`.
 */
export function wrapNear( value: number, ref: number, wrapSpan: number ): number {
    return ( ref + wrapDelta( value - ref, wrapSpan ) );
}

/**
 * Returns `delta + N*wrapSpan`, where `N` is a whole number and the returned
 * value is in `[ -0.5*wrapSpan, +0.5*wrapSpan )`.
 */
export function wrapDelta( delta: number, wrapSpan: number ): number {
    const wrapCount = round( delta / wrapSpan );
    return ( delta - ( wrapCount * wrapSpan ) );
}
