import { appendCssLink, createCssLink } from '@metsci/gleam-util';

const cssUrl = new URL( './defaults.css', import.meta.url );
const cssLink = createCssLink( cssUrl );
export function createGleamMvtDefaultStyle( ): HTMLLinkElement {
    return <HTMLLinkElement>cssLink.cloneNode( true );
}

const defaultStyle = appendCssLink( document.head, createGleamMvtDefaultStyle( ) );
export const gleamMvtDefaultStyleLoading = defaultStyle.loading;
export function removeGleamMvtDefaultStyle( ): void {
    defaultStyle.loading.catch( e => {
        if ( e.reason === 'DISPOSED' ) {
            // The dispose call below is expected to trigger this
        }
        else {
            throw e;
        }
    } );
    defaultStyle.disposers.dispose( );
}
